import React from 'react'

function Divider({ style, className }) {
  const divider_style = {
    width: '100%',
    height: 4,
    margin: '0.3rem 0',
    borderRadius: 10,
    backgroundImage:
      'linear-gradient(to right, rgba(255,255,255,0.7), rgba(255,255,255,0.5))',
    WebkitBackdropFilter: 'blur(50px)',
    backdropFilter: 'blur(50px)',
    boxShadow: '3px 3px 3px rgba(0,0,0,0.25)',
    ...style
  }

  return <div className={className} divider_style={divider_style} />
}

export default Divider
